

























import {Component, Vue, Provide, Watch} from 'vue-property-decorator';
import CertShow from '@/components/cert_card.vue';
import {Row, Col, NoticeBar, Button, Field, CellGroup, Cell, Divider} from 'vant';
import {CertOwn} from '../../model/certs_model';
import 'vant/lib/row/style';
import 'vant/lib/col/style';
import 'vant/lib/notice-bar/style';
import 'vant/lib/button/style';
import 'vant/lib/field/style';
import 'vant/lib/cell-group/style';
import 'vant/lib/cell/style';
import 'vant/lib/divider/style';

Vue.use(Row).use(Col).use(NoticeBar).use(Button).use(Field).use(CellGroup).use(Cell).use(Divider);

@Component({
    components: {CertShow},
})

export default class Show extends Vue {
    private certOwn!: CertOwn;
    private upChainInfo = '';           // 链上附加信息
    private isHaveChainMsg = false;

    public created(): void {
        this.$store.state.title = this.$t('cert.show');
        this.certOwn = this.$store.state.nowCertOwn;
        if (this.certOwn.attachInfo.inputData !== null && this.certOwn.attachInfo.inputData !== "") {
            this.isHaveChainMsg = true;
            this.upChainInfo = this.certOwn.attachInfo.inputData;
        } else {
            this.isHaveChainMsg = false;
        }
    }

    public mounted() {
    }

    public goOwnInput() {
        this.$router.push({name: 'own_input', params: {}});
    }
}
